import request from '@/utils/request'

const baseUrl = '/api/admin/rolePermission'

const rolePermissionApi = {}

rolePermissionApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

rolePermissionApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

rolePermissionApi.get = rolePermissionId => {
  return request({
    url: `${baseUrl}/${rolePermissionId}`,
    method: 'get'
  })
}

rolePermissionApi.create = rolePermission => {
  return request({
    url: `${baseUrl}`,
    data: rolePermission,
    method: 'post'
  })
}

rolePermissionApi.save = (rolePermission) => {
  return request({
    url: `${baseUrl}/save`,
    data: rolePermission,
    method: 'put'
  })
}

rolePermissionApi.delete = rolePermissionId => {
  return request({
    url: `${baseUrl}/${rolePermissionId}`,
    method: 'delete'
  })
}

export default rolePermissionApi
