import request from '@/utils/request'

const baseUrl = '/api/admin/permission'

const permissionApi = {}

permissionApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

permissionApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

permissionApi.get = permissionId => {
  return request({
    url: `${baseUrl}/${permissionId}`,
    method: 'get'
  })
}

permissionApi.tree = (params) => {
  return request({
    url: `${baseUrl}/tree`,
    params: params,
    method: 'get'
  })
}

permissionApi.create = permission => {
  return request({
    url: `${baseUrl}/save`,
    data: permission,
    method: 'post'
  })
}

permissionApi.update = (permission) => {
  return request({
    url: `${baseUrl}/update`,
    data: permission,
    method: 'put'
  })
}

permissionApi.delete = permissionId => {
  return request({
    url: `${baseUrl}/${permissionId}`,
    method: 'delete'
  })
}

function concreteTree(parentpermission, categories) {
  categories.forEach(permission => {
    if (parentpermission.key === permission.parentId) {
      if (!parentpermission.children) {
        parentpermission.children = []
      }
      parentpermission.children.push({
        key: permission.id,
        title: permission.name,
        isLeaf: false
      })
    }
  })

  if (parentpermission.children) {
    parentpermission.children.forEach(permission => concreteTree(permission, categories))
  } else {
    parentpermission.isLeaf = true
  }
}

permissionApi.concreteTree = categories => {
  const toppermissionNode = {
    key: 0,
    title: 'top',
    children: []
  }
  concreteTree(toppermissionNode, categories)
  return toppermissionNode.children
}

export default permissionApi
