import request from '@/utils/request'

const baseUrl = '/api/admin/role'

const roleApi = {}

roleApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

roleApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

roleApi.get = roleId => {
  return request({
    url: `${baseUrl}/${roleId}`,
    method: 'get'
  })
}

roleApi.tree = () => {
  return request({
    url: `${baseUrl}/tree`,
    method: 'get'
  })
}

roleApi.create = role => {
  return request({
    url: `${baseUrl}`,
    data: role,
    method: 'post'
  })
}

roleApi.update = (role) => {
  return request({
    url: `${baseUrl}`,
    data: role,
    method: 'put'
  })
}

roleApi.delete = roleId => {
  return request({
    url: `${baseUrl}/${roleId}`,
    method: 'delete'
  })
}

export default roleApi
