<template>
  <div>
    <el-row :gutter="20" style="display:flex;">
      <el-col class="mb20" :xl="18" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div style="margin-top: 20px;">
            <el-form :inline="true">
              <el-form-item label="名称">
                <el-input v-model="queryParams.title" placeholder="角色名称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getRoles()">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click="showAddForm = true;editParams={};selectedPremissionIds=[]"
                >新增</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div style="padding:20px;">
            <el-table
              :data="roles"
              style="margin-bottom:20px;"
              v-loading="tableLoading"
              element-loading-background="rgba(0, 0, 0, 0.3)"
              element-loading-text="数据正在加载中"
              element-loading-spinner="el-icon-loading"
            >
              <el-table-column prop="id" label="ID" width="80"></el-table-column>
              <el-table-column prop="name" label="名称"></el-table-column>
              <el-table-column prop="code" label="编码"></el-table-column>
              <el-table-column prop="description" label="描述" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="createTime" label="创建时间" width="180"></el-table-column>
              <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                  <el-button
                    type="text"
                    @click="getPermission(scope.row)"
                  >配置权限</el-button>
                  <el-button type="text" @click="editParams = scope.row;showAddForm = true">编辑</el-button>
                  <el-popconfirm
                    title="确定删除本条数据吗？删除后不可恢复。"
                    cancelButtonType="default"
                    style="margin-left:10px;"
                    @onConfirm="del(scope.row)"
                  >
                    <el-button type="text" v-if="scope.row.code!='admin'" slot="reference">删除</el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
            <pagination
              @sizeChange="handleSizeChange"
              @currentChange="handleCurrentChange"
              :pageNum="queryParams.pageNum"
              :pageSize="queryParams.pageSize"
              :total="total"
            ></pagination>
          </div>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="6" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>角色权限</span>
            <el-button
              v-if="editParams.id!=null"
              style="float: right; padding: 3px 0"
              type="text"
              @click="saveRolePermission"
            >保存</el-button>
          </div>
          <el-tree
            ref="permissionTree"
            :data="permissions"
            node-key="id"
            :props="{children: 'children',label: 'name'}"
            show-checkbox
            :default-checked-keys="selectedPremissionIds"
          ></el-tree>
        </el-card>
      </el-col>
    </el-row>
    <el-dialog title="编辑角色" width="500px" :visible.sync="showAddForm">
      <el-form :model="editParams" label-position="right" label-width="60px">
        <el-form-item label="名称">
          <el-input
            v-model="editParams.name"
            :disabled="editParams.code=='admin'"
            autocomplete="off"
            placeholder="角色名称"
          ></el-input>
        </el-form-item>
        <el-form-item label="编码">
          <el-input
            v-model="editParams.code"
            :disabled="editParams.code=='admin'"
            autocomplete="off"
            placeholder="例如：admin"
          ></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input
            type="textarea"
            v-model="editParams.description"
            autocomplete="off"
            placeholder="角色描述"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showAddForm = false">取消</el-button>
        <el-button type="primary" @click="saveRole">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/pagination";
import permissionApi from "@/api/permissionApi";
import roleApi from "@/api/roleApi";
import rolePermissionApi from "@/api/rolePermissionApi";
export default {
  components: {
    Pagination
  },
  data() {
    return {
      permissions: [],
      tableLoading: false,
      roles: [],
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0,
      showAddForm: false,
      editParams: {},
      selectedPremissionIds: []
    };
  },
  created() {
    this.getPermissionTree();
    this.getRoles();
  },
  methods: {
    getPermissionTree() {
      permissionApi.tree({ status: 1 }).then(res => {
        this.permissions = res.data;
      });
    },
    getRoles() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      this.tableLoading = true;
      roleApi.page(this.queryParams).then(res => {
        this.tableLoading = false;
        this.roles = res.data.records;
        this.total = res.data.total;
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getRoles();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getRoles();
    },
    saveRole() {
      if (this.editParams.id) {
        roleApi.update(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.showAddForm = false;
          this.getRoles();
        });
      } else {
        roleApi.create(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.showAddForm = false;
          this.getRoles();
        });
      }
    },
    del(row) {
      roleApi.delete(row.id).then(res => {
        this.$message.success("已删除");
        this.getRoles();
      });
    },
    getPermission(row) {
      this.$refs.permissionTree.setCheckedNodes([]);
      this.editParams = row;
      rolePermissionApi.findAll({ roleId: row.id }).then(res => {
        let ids = res.data.map(o => o.permissionId);
        this.selectedPremissionIds = ids;
      });
    },
    saveRolePermission() {
      let nodes = this.$refs.permissionTree.getCheckedNodes();
      if (nodes.length == 0) {
        this.$message.success("请选择权限");
        return false;
      }
      let arr = [];
      nodes.map(node => {
        let o = {};
        o.roleId = this.editParams.id;
        o.permissionId = node.id;
        arr.push(o);
      });
      rolePermissionApi.save(arr).then(res => {
        this.$message.success("保存成功");
      });
    }
  }
};
</script>

